@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;700&display=swap);
.form-item input:focus {
  outline: none;
  background-color: rgba(0, 153, 255, 0.15);
}

.form-item input[type='submit'] {
  color: white;
  background-color: rgba(0, 153, 255);
  font-weight: 700;
}

.form-item input[type='submit']:hover,
.form-item input[type='submit']:active {
  -webkit-filter: brightness(115%);
          filter: brightness(115%);
}

.form-item {
  margin: 0.5em;
}

.generic-form {
  margin-bottom: 0.5em;
  background-color: #f8f9fa;
  text-align: left;
  border-radius: 5px;
  transition: max-height 0.75s;
}

.inactive {
  max-height: 0;
  overflow: hidden;
}

.active {
  max-height: 1000px;
}

.cv-card {
  border-radius: 5px;
  border: 1px solid #ddd;
}

.cv-card-header {
  background-color: rgba(0, 0, 0, 0.6);
  border-bottom: 1px solid #ddd;
  padding: 0.5em;
  font-size: 1.2em;
}

.cv-card-header i {
  display: block;
  width: 25px;
  text-align: left;
}

.cv-card:hover .cv-card-header {
  background-color: rgb(0, 153, 255);
}

.cv-card-header strong {
  font-size: 0.7em;
  text-transform: uppercase;
}

.cv-card-body {
  padding: 0.5em;
}

.card-icon {
  display: block;
  width: 25px;
  margin-right: 0.4em;
  text-align: center;
}

.description-link {
  margin-bottom: 0.5em;
  display: flex;
  align-items: center;
  width: 100%;
}

.description-link i {
  font-size: 1.2em;
  display: block;
  width: 25px;
  margin-right: 0.5em;
  text-align: center;
}

@media (min-width: 700px) {
  .description-link {
    width: 50%;
  }
}

@media (min-width: 1000px) {
  .description-link {
    width: 25%;
    justify-content: center;
  }
}

.organisation {
  font-size: 1.5em;
}

.title {
  font-size: 1.15em;
}

.dates {
  font-size: 1.5em;
}

.card-item i {
  cursor: pointer;
  color: rgba(0, 0, 0, 0.6);
}

.card-item i:hover {
  color: black;
  transform: scale(1.1);
}

.brick {
  background-color: rgba(0, 0, 0, 0.6);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0.25em;
  position: relative;
}

.brick:hover {
  background-color: rgb(0, 153, 255);
}

.brick:hover .delete-brick {
  opacity: 1;
  pointer-events: auto;
}

.delete-brick {
  opacity: 0;
  position: absolute;
  cursor: pointer;
  pointer-events: none;
  top: -0.25em;
  right: -0.25em;
  color: #636364;
}

/* General styles */

* {
  font-family: 'Montserrat', sans-serif;
}

body {
  background-color: #eee;
}

button:focus {
  outline: none;
}

.card-action {
  border: none;
  background-color: transparent;
  opacity: 0.5;
}

.card-action:hover {
  opacity: 1;
  transform: scale(1.2);
}

